export default {
  data () {
    return {
      addr: wx.getStorageSync('addr') || {},
      goodsList: []
    }
  },
  computed: {
    detailAddr () {
      let {provinceName, cityName, countyName, detailInfo} = this.addr
      return provinceName + cityName + countyName + detailInfo
    },
    totalPrice () {
      return this.goodsList.reduce(function (sum, v) {
        return sum + (v.num * v.goods_price)
      }, 0)
    }
  },
  onLoad (options) {
    this.goodsId = options.goodsId
    this.getGoodsList()
  },
  methods: {
    pay () {
      // 是否有商品，是否选择了地址，如果满足，就创建订单
      if (!this.totalPrice) {
        wx.showToast({
          title: '请添加商品', // 提示的内容,
          icon: 'none'
        })
        return
      }
      if (!this.addr.userName) {
        wx.showToast({
          title: '请选择收货地址', // 提示的内容,
          icon: 'none'
        })
        return
      }
      this.createOrder()
    },
    // 创建订单
    async createOrder () {
      let orderData = await this.$request({
        url: '/api/public/v1/my/orders/create',
        method: 'POST',
        isAuth: true,
        data: {
          order_price: this.totalPrice,
          consignee_addr: this.detailAddr,
          goods: this.filterGoodsList()
        }
      })
      // 无论成功或者失败，都应该把该订单的商品从购物车去掉
      !this.goodsId && this.arrangeCart()
      this.orderNumber = orderData.order_number
      this.doPay()
    },
    // 删除购物车里面的checked的商品
    arrangeCart () {
      this.$store.commit('arrangeCart')
    },
    // 创建支付单
    async doPay () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/req_unifiedorder',
        method: 'POST',
        isAuth: true,
        data: {
          order_number: this.orderNumber
        }
      })
      wx.requestPayment({
        ...data.pay,
        success (res) {
          // 成功跳转订单结果页
          wx.navigateTo({ url: '/pages/order_result/main' })
        },
        fail: () => {
          // 成功跳转订单结果页
          wx.navigateTo({ url: '/pages/order_result/main?orderNumber=' + this.orderNumber })
        }
      })
    },
    filterGoodsList () {
      // let goods = []
      // this.goodsList.forEach(v => {
      //   goods.push({
      //     goods_id: v.goods_id,
      //     goods_number: v.goods_number,
      //     goods_price: v.goods_price
      //   })
      // })
      return this.goodsList.map(v => {
        return {
          goods_id: v.goods_id,
          goods_number: v.num,
          goods_price: v.goods_price
        }
      })
    },
    // 返回购物车中checked的商品的id
    filterCartIds (cart) {
      let ids = []
      for (let id in cart) {
        if (cart[id].checked) {
          ids.push(id)
        }
      }
      return ids.join(',')
    },
    async getGoodsList () {
      let cart = this.$store.getters.getCart || []
      // 如果有goodsId的话，那么查询该goodsId商品列表
      if (this.goodsId) {
        cart = {
          [this.goodsId]: {
            num: 1,
            checked: true
          }
        }
      }
      // 过滤掉购的车里面unchecked的商品
      let ids = this.filterCartIds(cart)
      // 购物车数据为空的，不必请求
      if (!ids) {
        return
      }
      let goodsList = await this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + ids
      })
      // cart和goodsList数据融合
      goodsList.forEach(v => {
        v.num = cart[v.goods_id].num
        v.checked = cart[v.goods_id].checked
      })

      this.goodsList = goodsList
    },
    getAddr () {
      wx.getSetting({ success: res => {
        if (res.authSetting['scope.address'] === false) {
          // 如果授权拒绝，提示用户打开允许
          wx.showModal({
            title: '提示',
            content: '请在设置允许通讯地址',
            success: res => {
              if (res.confirm) {
                wx.openSetting()
              }
            }
          })
        } else {
          wx.authorize({
            scope:
              'scope.address',
            success: () => {
              // 如果授权允许，如果第一次授权，逻辑都是先请求授权，再调用对应的接口获取数据
              wx.chooseAddress({
                success: (addrRes) => {
                  this.addr = addrRes
                  wx.setStorageSync('addr', this.addr)
                }
              })
            }
          })
        }
      } })
    }
  }
}
